import { Navigate } from "react-router-dom";
import { getUserRole } from "../api/auth";
import { hasPermission, getDefaultDashboard } from "../config/permissions";

/**
 * Componente para proteger rotas baseado no role do usurio
 * Redireciona para o dashboard correto se o usurio no tiver permisso
 */
export default function ProtectedRoute({ children, allowedRoles = [] }) {
  const token = localStorage.getItem("access_token");
  const userRole = getUserRole();

  // Se no estiver autenticado, redireciona para login
  if (!token) {
    return <Navigate to="/login" replace />;
  }

  // Se no tiver role definido, redireciona para login
  if (!userRole) {
    return <Navigate to="/login" replace />;
  }

  // Se a rota tem restrio de roles e o usurio no est na lista
  if (allowedRoles.length > 0 && !allowedRoles.includes(userRole)) {
    // Redireciona para o dashboard correto do usurio
    const dashboard = getDefaultDashboard(userRole);
    return <Navigate to={dashboard} replace />;
  }

  return children;
}

